import React from "react"
import cf from "currency-formatter"
import AlreadyUsed from "./AlreadyUsed"

const Main = ({
  startHandle,
  onType,
  salary,
  startWithPrevDate,
  alreadyUsed,
}) => (
  <main>
  <div className="container">
    <div className="intro">
      <p>
        Salary Meters calculates how much you earn in a day, seconds or even years. Also it shows you interesting facts about your income.
      </p>
    </div>
    {alreadyUsed && <AlreadyUsed startWithPrevDate={startWithPrevDate} /> }
    <div className="input_wrap">
      {/* salary != "" && <span>{cf.format(salary, {code: "INR"})}</span> */}
      <input
        type="number"
        placeholder="How much you earn per month?"
        id="sal"
        name="salary"
        value={salary}
        onChange={onType}
      />
      <button onClick={startHandle}>Start</button>
    </div>
    <h6>Enter your Salary in ruppees.</h6>
    <span className="reset hide">Reset</span>
  </div>
</main>
)

export default Main



