import React from "react"

const AlreadyUsed = ({
  startWithPrevDate,
}) => (
  <div className="already_used">
    <h6>Would like to calculate based on previous salary</h6>
    <button>No!</button>
    <button onClick={startWithPrevDate}>Hell yeah!!</button>
  </div>
)

export default AlreadyUsed



