import moment from "moment"

export function salaryBreakdown(sal) {
  const perDay = sal/30;
  const perHr = perDay/24
  const perWorkingHr = perDay/9
  const perMin = perHr/60
  const perSec = perMin/60
  return {
    perDay,
    perWorkingHr,
    perHr,
    perMin,
    perSec,
  }
}


export function timeToEarn(cost, salaryPerMin){
  const d = parseFloat(cost/salaryPerMin)
  const duration = d.toFixed(2);
  if (duration < 1 ){
    return `${parseInt(duration*100)} secs`
  } else if( duration > 1 && duration < 60){
    return `${duration} mins`
  } else if( duration > 60 && duration < (60 * 24)){
    return `${parseFloat(duration/60).toFixed(2)} hrs`
  } else if(duration > (60 * 24) && duration < (60 * 24 * 60)){
    return `${parseInt(duration/(60 * 24))} days`
  }else if(duration > (60 * 24 * 60) && duration < (60 * 24 * 365)){
    const days = parseInt((duration%(60 * 24 * 30))/( 60 * 24))
    const months = parseInt(duration/(60 * 24 * 30))
    return `${days} days and ${months} months`
  }

  return `${duration} mins`
}

export function initMoment(){
  moment.updateLocale('en', {
      relativeTime : {
          future: "in %s",
          past:   "%s ago",
          s: function (number, withoutSuffix, key, isFuture){
              return '00:' + (number<10 ? '0':'') + number + ' minutes';
          },
          m:  "01:00 minutes",
          mm: function (number, withoutSuffix, key, isFuture){
              return (number<10 ? '0':'') + number + ':00' + ' minutes';
          },
          h:  "an hour",
          hh: "%d hours",
          d:  "a day",
          dd: "%d days",
          M:  "a month",
          MM: "%d months",
          y:  "a year",
          yy: "%d years"
      }
  });
}



