import React from "react"
import moment from "moment"
import cf from "currency-formatter"

const INDIAS_AVG = 9072

const getSalaryCompare = (salary) => {
  const per = parseInt((salary/INDIAS_AVG) * 100)
  if (per <= 100) {
    const diff = 100 - per;
    return <h3>Your Salary <span>{diff}%</span> is less than average Indian.</h3>
  } else {
    return <h3>Your Salary is <span>{per}%</span> more than average Indian.</h3>
  }
}

const Running = ({
  earned,
  timePassed,
  reset,
  end,
  salary,
  salaryBreakUp: {
    perHr,
    perDay,
    perMin,
    perSec,
  }
}) => (
<div className="running">
  <div className="container">
    <h1>Your earned  <span id="amt">{earned.toFixed(2)}</span>/- </h1>

    <h6 className="since">from the time your started, {moment(timePassed).fromNow()}</h6>
    <div className="btn_wrap">
      <button onClick={end}>End</button>
    </div>

    <div className="breakdown">
      <h3>You Earn,</h3>
      <div>
        <p>In a day</p>
        <span>{cf.format(perDay, {code: "INR"})}</span>
      </div>
      <div>
        <p>In an hour</p>
        <span>{cf.format(perHr, {code: "INR"})}</span>
      </div>
      <div>
        <p>In is minute</p>
        <span>{cf.format(perMin, {code: "INR"})}</span>
      </div>
      <div>
        <p>In a second</p>
        <span>{cf.format(perSec, {code: "INR"})}</span>
      </div>
    </div>

    <h2>{getSalaryCompare(salary)}</h2>
  </div>
</div>
)

export default Running



