export function isAlreadyUsed(){
  const sm = localStorage.getItem("sm")
  return !!sm
}

export function saveIt(data){
  try{
    localStorage.setItem("sm", JSON.stringify(data))
  }catch(e){
    console.log(e);
  }
}

export function getSavedState(){
  try{
    const ls = localStorage.getItem("sm")
    console.log(ls);
    return JSON.parse(ls)
  }catch(e){
    console.log(e);
  }
}



