import React, { Component } from 'react';
import moment from "moment"
import { salaryBreakdown, initMoment } from "./utils"
import { isAlreadyUsed, saveIt, getSavedState } from "./ls"
import Main from "./components/Main"
import Running from "./components/Running"
import Header from "./components/Header"
import Stats from "./components/Stats"
import Developer from "./components/Developer"
import Footer from "./components/Footer"
import './styles/main.css';
import Items from "./items.js"

class App extends Component {

  state = {
    isRunning: false,
    earned: 0.0,
    salary: "40000"
  }

  compoentWillMount(){
    initMoment();
  }
  componentDidMount(){
    if(isAlreadyUsed()){
      const state = getSavedState();
      if(state){
        this.setState({
          alreadyUsed: true,
        })
      }
    }
  }

  start = () => {
    const { salary } = this.state
    if( salary !== ""){
      const salaryBreakUp = salaryBreakdown(salary)
      this.setState({
        isRunning: true,
        startTime: moment(),
        salaryBreakUp,
      })
      this.tickTick()
    }else{
      alert("Please enter salary to start");
    }
  }

  end = () => {
    clearInterval(this.TASK)
    this.setState({
      isRunning:false,
      alreadyUsed: true,
    })
  }

  reset = () => {
    localStorage.clear();
    this.setState({
      isRunning: false,
      earned: 0.0,
      salary: ""
    })
  }

  handleType = (e) => {
    this.setState({
      [e.target.name]: e.target.value
    })
  }

  startWithPrevDate = () => {
    const state = getSavedState();
    const startTime = moment(state.startTime)
    this.setState({
      ...state,
    }, () => {
      this.tickTick()
    })
  }

  tickTick = () => {
    this.TASK = setInterval(() => {
      const { salaryBreakUp, earned, startTime } = this.state
      const currenTime = moment();
      const times = moment(currenTime).unix() - moment(startTime).unix()
      console.log(moment(currenTime).unix() - moment(startTime).unix());
      const { perSec } = salaryBreakUp
      const newEarned = perSec * times;
      this.setState({
        earned: newEarned,
      }, () => {
        saveIt(this.state)
      })
    }, 1000)
  }

	render(){
    const {
      isRunning,
      salary,
      startTime,
      earned,
      salaryBreakUp,
      alreadyUsed,
    } = this.state
    console.log(this.state)
		return(
      <div>
        <Header />
        { isRunning
          ? <div>
              <Running
                salary={salary}
                earned={earned}
                timePassed={startTime}
                end={this.end}
                reset={this.reset}
                salaryBreakUp={salaryBreakUp || {}}
              />
              <Stats
                earned={earned}
                salaryPerMin={salaryBreakUp && salaryBreakUp.perMin}
                items={Items}
              />
              <Developer />
            </div>
          : <Main
              alreadyUsed={alreadyUsed}
              startHandle={this.start}
              salary={salary}
              onType={this.handleType}
              startWithPrevDate={this.startWithPrevDate}
            />
        }
        <Footer />
      </div>
    )
	}
}

export default App;



