import React from "react"
import fb from "./../images/fb.png"
import tw from "./../images/yt.png"
import yt from "./../images/tw.png"

const Developer = () => (
  <div className="social">
    <div className="container">
      <div>
        <a href="https://facebook.com/ramanchoudhry" target="_blank"><img src={fb} alt="Facebook"/></a>
        <a href="https://twitter.com/ramancodians" target="_blank"><img src={tw} alt="Twitter"/></a>
        <a href="https://www.youtube.com/channel/UC4_FNIaX2nLAidZpc0ZZmNQ" target="_blank"><img src={yt} alt="YouTube"/></a>
      </div>
    </div>
  </div>
)

export default Developer



